function Cart(props){

const { cartitem,onRemove,setButtons } = props;

    return(
        

        <div class="list-group">
          
 
         
          
   {
   cartitem.map((item)=>{
       return (
           <div className="list-group=item">{item.itemNo} - ${item.price}
                <button className="btn btn-sm btn-danger ml-1"  onClick={()=>{
                        onRemove(item);
                        
                }}
                    > X </button>
           </div>
       );
    })
    
   }
         
         
    
       </div>
    );
}

export default Cart;